.hamburger{
    display: none;
}

.menu {
display: none;
}


@media only screen and (max-width: 800px){
    .hamburger{
        display: initial;
        z-index: 4;
        position: fixed;
        top: 20px;
        left: 20px;
        transform: scale(1);
        transition: transform 200ms;
        
    
        &:hover{
            cursor: pointer;
            transform: scale(1.1);
            transition: transform 200ms;    
        }
    }
    
    .menu{
    z-index: 3;
    width: 70vw;
    height: 100%;
    position: fixed;
    clip-path: polygon(0 0, 85% 0, 100% 100%, 0% 100%);
    top: 0;
    left: 0;
    transition: left 0.8s ease;
    background-color: white;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    
    .itemContainer{
        margin-top: 10vh;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .menuItem{
        padding-left: 6vw;
        text-align: left;
        font-size: 6vw;
        text-decoration-style: solid;
        text-decoration-color: rgb(0, 0, 0);
        text-decoration-thickness: 2px;
    }
}

.hidden {
    left: -71vw;
}
}